<script>
import { dispose, init } from "klinecharts";
export default {
    name: "Futures",
    components: {},
    data() {
        return {
            loading: false,
            ws: null,
            wsTemp: 0,
            kLineChart: null,
            klineData: [],
            isUpdateKlineData: false,
            symbol: "btcusdt",
            symbolName: "",
            size: 365,
            main: {
                symbol: "",
                price: 0,
                percent: 0,
            },
            tick: {
                open: 0,
                close: 0,
                percent: 0,
            },

            radio: {
                value: "1min",
            },
            slider: {
                marks: {
                    0: { style: { color: '#999' }, label: "0x", },
                    25: { style: { color: '#999' }, label: "25x", },
                    50: { style: { color: '#999' }, label: "50x", },
                    75: { style: { color: '#999' }, label: "75x", },
                    100: { style: { color: '#999' }, label: "100x", },
                },
            },
            menu: {
                current: ["limit"],
            },
            list: [],
            orderbook_buy_list: [],
            orderbook_sell_list: [],
            market_trades_list: [],

            currMarketBuyPrice: 0,
            currMarketBuyAmount: 0,
            currMarketBuySum: 0,
            currMarketBuyLever: 0,

            currMarketSellPrice: 0,
            currMarketSellAmount: 0,
            currMarketSellSum: 0,
            currMarketSellLever: 0,

            // 合约账户USDT数量
            currContractUsdtAmount: 0,
            // 合约历史记录
            CoutractHistory: [],

            // 计划任务队列
            CronArr: [],
            isCronJobStop: false,

            wsHuobi: null,
            wsHuobiTemp: 0,

            // 调节杠杆 显示的组件样式 1滑拉 2选择项
            leverStyleType: 2,

            // Modal
            loading_modal: false,
            modal: {
                action: '-',
                visible: false,
                data: {}
            },

            // 当前货币属性和数据
            currSymbolData: null,


        };
    },
    destroyed: function () {
        dispose("custom-theme-k-line");
        if (this.ws != null) {
            this.ws.close();
            this.ws = null;
            clearTimeout(this.wsTemp)
        }
        if (this.wsHuobi != null) {
            this.wsHuobi.close();
            this.wsHuobi = null
            clearTimeout(this.wsHuobiTemp)
        }
        this.isCronJobStop = true;
    },
    watch: {
        currMarketBuyPrice() {
            this.updateBuySum()
        },
        currMarketBuyAmount() {
            this.updateBuySum()
        },

        currMarketSellPrice() {
            this.updateSellSum()
        },
        currMarketSellAmount() {
            this.updateSellSum()
        }
    },
    methods: {
        onClickModalOk() {
            this.loading_modal = true
            console.log(this.modal.data)
            $.post(`${this.g.apiUrl}/api/currency-contract/${this.modal.action}/trade`, this.modal.data, (res) => {
                if (res.flag != "ok") {
                    this.$message.error(res.flag)
                    this.loading_modal = false
                    return
                }

                console.log(res)
                this.$message.success("succeed!")

                setTimeout(() => {
                    this.loading_modal = false
                    this.modal.visible = false
                    this.initContractUsdtAmount()
                    this.initCoutractHistory()
                }, 1000)

            }).fail(err => {
                this.$message.error(err.responseJSON.error)
                setTimeout(() => {
                    this.loading_modal = false
                }, 1000)
            })
        },
        onClickModalCancel(e) {
            this.loading_modal = false
        },
        onClickBuyLong() {
            if (this.checkLogin()) {
                if (this.currContractUsdtAmount < this.currMarketBuySum) {
                    this.$message.warning('not sufficient funds')
                    return
                }
                if (this.currMarketBuySum <= 0) {
                    this.$message.warning('Please fill in the amount!')
                    return
                }
                this.modal.action = 'buy'
                this.modal.data = {
                    lots: this.currMarketBuyAmount,
                    multiple: this.currMarketBuyLever,
                    handling_fee: this.g.round2(this.currMarketBuyPrice * 0.001),
                    margin: this.currMarketBuySum,
                    price: this.currMarketBuyPrice,
                    symbol: this.symbol,
                    user_id: this.g.userInfo.id,
                }
                this.modal.visible = true
            }
        },
        onClickBuyShort() {
            if (this.checkLogin()) {
                if (this.currContractUsdtAmount < this.currMarketSellSum) {
                    this.$message.warning('not sufficient funds')
                    return
                }
                if (this.currMarketSellSum <= 0) {
                    this.$message.warning('Please fill in the amount!')
                    return
                }
                this.modal.action = 'sell'
                this.modal.data = {
                    lots: this.currMarketSellAmount,
                    multiple: this.currMarketSellLever,
                    handling_fee: 0.002,
                    margin: this.currMarketSellSum,
                    price: this.currMarketSellPrice,
                    symbol: this.symbol,
                    user_id: this.g.userInfo.id,
                }
                this.modal.visible = true
            }
        },
        onClickOrderbook(item, direction) {
            console.log("item", item, direction);
        },
        onClickSymbol(item) {
            // console.log("item", item.symbol, item);
            const symbol = item.symbol.toLocaleLowerCase();
            this.$router.push(`/futures?symbol=${symbol}usdt`);
            window.location.reload();
        },
        onChangeCallback(key) {
            console.log(key);
        },
        onChangeBuySlider(e) {
            this.updateBuySum()
        },
        onChangeSellSlider(e) {
            this.updateSellSum()
            console.log(e)
        },
        onChangeKline(e) {
            console.log(`checked = ${e.target.value}`);
            // { key: 'candle_solid', text: '蜡烛实心' },
            // { key: 'candle_stroke', text: '蜡烛空心' },
            // { key: 'candle_up_stroke', text: '蜡烛涨空心' },
            // { key: 'candle_down_stroke', text: '蜡烛跌空心' },
            // { key: 'ohlc', text: 'OHLC' },
            // { key: 'area', text: '面积图' }
            const period = e.target.value;

            if (period == "line") {
                this.kLineChart.setStyleOptions({
                    candle: { type: "area" },
                });
                return;
            } else {
                this.kLineChart.setStyleOptions({
                    candle: { type: "candle_solid" },
                });
            }

            this.loading = true;
            this.getKLineDataList(this.symbol, period, this.size, (res) => {
                this.loading = false;
                this.klineData = res;
                this.kLineChart.applyNewData(this.klineData);
            });
        },
        checkLogin() {
            if (!this.g.userInfo) {
                this.$router.push('/login')
                return false;
            }
            return true;
        },
        getKLineDataList(symbol, period, size, callback) {
            let query = {
                symbol,
                period,
                size,
            }
            if (this.currSymbolData.type != "") {
                query.symbol_type = this.currSymbolData.type
            }
            const url = this.g.apiUrl + `/api/kline`;
            $.get(url, query, (res) => {
                let a = [];
                a = res.data.data.reverse();
                a.forEach(function (item) {
                    item.timestamp = item.id * 1000;
                    item.volume = item.vol;
                });
                if (callback) {
                    callback(a);
                }
            });
        },
        updateBuySum() {
            if (this.currMarketBuyLever > 0) {
                this.currMarketBuySum = this.g.round(this.currMarketBuyPrice * this.currMarketBuyAmount / this.currMarketBuyLever, 1, 8)
            } else {
                this.currMarketBuySum = this.g.round(this.currMarketBuyPrice * this.currMarketBuyAmount, 1, 8)
            }
        },
        updateSellSum() {
            if (this.currMarketSellLever > 0) {
                // console.log(this.currMarketSellLever, this.g.round(this.currMarketSellPrice * this.currMarketSellAmount / this.currMarketSellLever, 1, 8))
                this.currMarketSellSum = this.g.round(this.currMarketSellPrice * this.currMarketSellAmount / this.currMarketSellLever, 1, 8)
            } else {
                // console.log(this.currMarketSellLever, this.g.round(this.currMarketSellPrice * this.currMarketSellAmount, 1, 8))
                this.currMarketSellSum = this.g.round(this.currMarketSellPrice * this.currMarketSellAmount, 1, 8)
            }
        },
        updateCandlestick(res) {
            // 更新价格和百分比
            this.tick = res.tick;
            this.main.price = res.tick.close;
            this.main.percent = Math.round(((this.tick.close - this.tick.open) / this.tick.close) * 10000) / 100;

            // 新数据
            // let mm = moment(res.ts).format("YYYYMMDDHHmm");
            if (this.isUpdateKlineData) {
                this.isUpdateKlineData = false;
                let o = {
                    timestamp: res.ts,
                    open: res.tick.close,
                    close: res.tick.close,
                    high: res.tick.close,
                    low: res.tick.close,
                    volume: this.g.random(res.tick.low, res.tick.high),
                };
                this.klineData.push(o);
                //更新 指标【量】的数据
                this.getKLineDataList(this.symbol, this.radio.value, 2, (res) => {
                    // console.log('更新 指标【量】的数据')
                    // console.log(res)
                    this.klineData[this.klineData.length - 1].volume = res[1].vol
                    this.klineData[this.klineData.length - 2].volume = res[0].vol
                    this.kLineChart.updateData(o);
                });
                return;
            }
            // 更新旧数据
            let lastData = this.klineData[this.klineData.length - 1];
            if (!lastData || !lastData.close) {
                return
            }
            lastData.close = res.tick.close;
            lastData.high = Math.max(lastData.high, lastData.close);
            lastData.low = Math.min(lastData.low, lastData.close);
            lastData.volume = this.g.random(lastData.low, lastData.high);
            this.kLineChart.updateData(lastData);
        },
        updateMarketList(name, res) {
            for (let i = 0; i < this.list.length; i++) {
                let item = this.list[i];
                if (item.name == name) {
                    item.priceUsd = this.g.round2(res.tick.close, 4);
                    break;
                }
            }
        },
        updateBuySellList(res) {
            // console.log(res)
            let a = res.tick.data
            for (let i = 0; i < a.length; i++) {
                let item = a[i]
                item.id = this.g.round(Math.random() * 100000000, 1, 8)
                item.amount = this.g.round(item.amount)
                item.sum = this.g.round(Math.random(), 1)
                item.time = moment(item.ts).format("HH:mm:ss")
                // console.log(item)
                this.market_trades_list.pop();
                this.market_trades_list.splice(0, 0, item);
                if (item.direction == "buy") {
                    this.currMarketBuyPrice = getBuyMin(this.orderbook_buy_list)
                    this.orderbook_buy_list.pop();
                    this.orderbook_buy_list.splice(0, 0, item);
                    this.orderbook_buy_list = percent(this.orderbook_buy_list)
                } else if (item.direction == "sell") {
                    this.currMarketSellPrice = getSellMax(this.orderbook_sell_list)
                    this.orderbook_sell_list.pop();
                    this.orderbook_sell_list.splice(0, 0, item);
                    this.orderbook_sell_list = percent(this.orderbook_sell_list)
                }
            }
        },
        getPercent(item) {
            const resNum = item.close - item.open
            const p1 = resNum / item.close * 100
            const p2 = this.g.round(p1, 1, 2)
            let p3 = p2
            if (p2 > 0) {
                p3 = `+${p2}`
            }
            return p3
        },
        initKLineChart() {
            this.kLineChart = init("custom-theme-k-line");
            this.kLineChart.setStyleOptions(getThemeOptions("dark"));
            this.kLineChart.createTechnicalIndicator("VOL", true);

            this.kLineChart.createTechnicalIndicator('MA', false, { id: 'candle_pane' })
            // this.kLineChart.createTechnicalIndicator('KDJ', false, { height: 80 })


            this.kLineChart.setStyleOptions(getLanguageOptions(""));
            this.loading = true;
            this.getKLineDataList(this.symbol, this.radio.value, this.size, (res) => {
                this.loading = false;
                this.klineData = res;
                this.kLineChart.applyNewData(this.klineData);
            });
        },
        cronJobCallback(time_type) {
            if (this.isCronJobStop) {
                console.log('1 this.isCronJobStop', this.isCronJobStop, time_type)
                return
            }
            // console.log(time_type, new Date().toLocaleString());
            if (time_type == this.radio.value) {
                console.log('2 this.isCronJobStop', this.isCronJobStop, time_type)
                this.isUpdateKlineData = true
            }
        },
        initCronJob(callback) {
            // const star = new CronJob("* * * * * *", function () {
            //     if (callback) {
            //         callback("*");
            //     }
            // })
            // this.CronArr.push(star);

            const m1 = new CronJob("0 * * * * *", function () {
                if (callback) {
                    callback("1min");
                }
            })
            this.CronArr.push(m1);

            const m5 = new CronJob("0 0/5 * * * *", function () {
                if (callback) {
                    callback("5min");
                }
            })
            this.CronArr.push(m5);

            const m15 = new CronJob("0 0/15 * * * *", function () {
                if (callback) {
                    callback("15min");
                }
            })
            this.CronArr.push(m15);

            const m30 = new CronJob("0 0/30 * * * *", function () {
                if (callback) {
                    callback("30min");
                }
            })
            this.CronArr.push(m30);

            const h1 = new CronJob("0 0 0/1 * * *", function () {
                if (callback) {
                    callback("1h");
                }
            })
            this.CronArr.push(h1);

            const h4 = new CronJob("0 0 0/4 * * *", function () {
                if (callback) {
                    callback("4h");
                }
            })
            this.CronArr.push(h4);

            const d1 = new CronJob("0 0 0 1/1 * *", function () {
                if (callback) {
                    callback("1d");
                }
            })
            this.CronArr.push(d1);
        },
        initWS() {
            this.ws = new WebSocket(`${this.g.wsUrl}/market.${this.symbol}.ticker`);
            this.ws.onopen = () => {
                console.log("websocket init ...");
                // this.ws.send(JSON.stringify({ test: "测试数据" }));
            };
            this.ws.onclose = () => {
                console.log("websocket close ...");
                this.wsTemp = setTimeout(() => {
                    if (this.ws != null) {
                        this.initWS();
                    }
                }, 1000);
            };
            this.ws.onmessage = (event) => {
                let res = JSON.parse(event.data);
                if (res.ch == "market.btcusdt.ticker" && res.tick.close != undefined && this.klineData.length > 0) {
                    this.updateMarketList('btc', res)
                    this.updateCandlestick(res)
                }
            };
        },
        initWSHuobi() {
            let _t = this
            let symbol = _t.symbol
            let market = {
                ticker: {
                    "sub": `market.${symbol.toLocaleLowerCase()}.ticker`
                },
                trade_detail: {
                    "sub": `market.${symbol.toLocaleLowerCase()}.trade.detail`,
                    "id": "id1"
                }
            }

            this.wsHuobi = new WebSocket("wss://api.huobi.pro/ws")
            this.wsHuobi.onopen = () => {
                console.log('huobi api connection open...')
                this.wsHuobi.send(JSON.stringify(market.ticker))
                this.wsHuobi.send(JSON.stringify(market.trade_detail))
            };
            this.wsHuobi.onclose = () => {
                console.log('huobi api connection close...')
                // reconnection
                clearTimeout(this.wsHuobiTemp)
                this.wsHuobiTemp = setTimeout(() => {
                    this.initWSHuobi()
                }, 1000 * 60)
            }
            this.wsHuobi.onmessage = (event) => {
                const _t = this
                // console.log('huobi api connection message...')
                const fr = new FileReader();
                fr.onload = function () {
                    let data = pako.ungzip(new Uint8Array(fr.result), { to: 'string' })
                    data = JSON.parse(data)
                    if (data.ping) {
                        // console.log('pong', data.ping)
                        _t.wsHuobi.send(JSON.stringify({ pong: data.ping }))
                    } else if (data.ch == `market.${_t.symbol.toLocaleLowerCase()}.ticker`) {
                        // console.log(data)
                        _t.updateMarketList(_t.symbolName.toLocaleLowerCase(), data)
                        _t.updateCandlestick(data)
                    } else if (data.ch == `market.${_t.symbol.toLocaleLowerCase()}.trade.detail`) {
                        // console.log(data)
                        _t.updateBuySellList(data)
                    } else {
                        console.log('huobi api ws other data ： ', data)
                    }
                }
                fr.readAsArrayBuffer(event.data);
            }
        },
        showListHuobi(a) {
            const _t = this;
            a.forEach(function (item) {
                item.symbol = item.name.toLocaleUpperCase()
                item.priceUsd = _t.g.round2(item.close, 4);
                item.changePercent24Hr = _t.getPercent(item);
            });
            this.list = a;
        },
        initHuobiList() {
            const url = this.g.apiUrl + "/api/caches?key=huobi-market-tickers";
            $.get(url, (res) => {
                let a = res.data.value;
                this.showListHuobi(a)
            });
        },
        initBuySellList(symbolData) {
            console.log('initBuySellList', symbolData)
            const RowCount = 7;
            let a = [];
            for (let i = 0; i < RowCount; i++) {
                let item = {
                    price: this.g.round2(this.g.random(symbolData.close, symbolData.close + 2)),
                    amount: this.g.round(Math.random(), 1),
                    sum: this.g.round(Math.random(), 1),
                    percent: 0,
                };
                a.push(item);
            }
            this.orderbook_sell_list = percent(a);
            this.currMarketSellPrice = getBuyMin(this.orderbook_sell_list)

            a = [];
            for (let i = 0; i < RowCount; i++) {
                let item = {
                    price: this.g.round2(this.g.random(symbolData.close - 2, symbolData.close)),
                    amount: this.g.round(Math.random(), 1),
                    sum: this.g.round(Math.random(), 1),
                    percent: 0,
                };
                a.push(item);
            }
            this.orderbook_buy_list = percent(a);
            this.currMarketBuyPrice = getBuyMin(this.orderbook_buy_list)
        },
        initTickerList(symbolData) {
            let a = [];
            for (let i = 0; i < 30; i++) {
                let item = {
                    id: this.g.round(Math.random() * 100000000, 1, 8),
                    time: moment().format("HH:mm:ss"),
                    price: this.g.round2(this.g.random(symbolData.close, symbolData.close + 2)),
                    amount: this.g.round(Math.random(), 1),
                    act: Math.floor(Math.random() * 10) > 5 ? "buy" : "sell",
                };
                a.push(item);
            }
            this.market_trades_list = a;
        },
        initContractUsdtAmount() {
            if (!this.g.userInfo) {
                return;
            }
            setTimeout(() => {
                $.get(`${this.g.apiUrl}/api/my/assets`, { user_id: this.g.userInfo.id }, (res) => {
                    this.currContractUsdtAmount = getWallet(res.data.walletList, 'usdt').contract_amount
                })
            }, 1000)

        },
        initCoutractHistory() {
            $.get(`${this.g.apiUrl}/api/currency-contract/history`, { user_id: this.g.userInfo.id }, (res) => {
                console.log(res)
                this.CoutractHistory = res.data.list
            }).fail((err) => {
                this.$message.error(err.statusText)
            })
        },
        init() {
            // 交易对 小写
            this.symbol = this.g.getQueryVariable("symbol");
            // 当前货币名称 大写
            this.symbolName = this.symbol.substring(0, this.symbol.length - 4).toLocaleUpperCase()

            // 货币列表
            // this.initHuobiList() // 旧 http方式
            const arr = this.g.appWsData
            this.showListHuobi(arr)
            this.$EventBus.$on("appWsComplete", (arr) => {
                this.showListHuobi(arr)
            })

            // 主要获取当前交易对的实时价格好生成交易列表
            this.currSymbolData = getCoinData(arr, this.symbolName.toLocaleLowerCase())
            this.initBuySellList(this.currSymbolData)
            this.initTickerList(this.currSymbolData)
            // 我的合约币数量
            this.initContractUsdtAmount()
            // 合约历史交易记录
            this.initCoutractHistory()

            // 初始化K线图
            this.initKLineChart()

            // 当前货币 火币API 实时数据
            // this.initWS();
            console.log("=========================================")
            console.log(this.currSymbolData)
            if (this.currSymbolData.type == "currency_contract" || this.currSymbolData.type == "currency_paltform") {
                console.log("平台币，合约币")
            } else {
                // 火币实时行情 和 交易单数量
                this.initWSHuobi();
                // 定时 更新K线图
                this.initCronJob(this.cronJobCallback);
            }



            // KLineChart大小调整
            let klineChartTemp = 0
            window.addEventListener("resize", () => {
                clearTimeout(klineChartTemp)
                klineChartTemp = setTimeout(() => {
                    this.kLineChart.resize();
                }, 100)
            });
        },
    },
    mounted() {
        this.init();
    },
};

// 功能函数
function percent(a) {
    let _a = [];
    let max_price = 0;
    for (let i = 0; i < a.length; i++) {
        max_price = Math.max(max_price, Number(a[i].sum));
        a[i].percent = 0
    }
    for (let i = 0; i < a.length; i++) {
        let item = a[i];
        item.percent = Math.round((item.sum / max_price) * 100);
        _a.push(item);
    }
    return _a;
}
function getBuyMin(a) {
    if (a.length <= 0) {
        return
    }
    let min = a[0].price
    for (let i = 0; i < a.length; i++) {
        min = Math.min(min, a[i].price)
    }
    return min
}
function getSellMax(a) {
    if (a.length <= 0) {
        return
    }
    let max = a[0].price
    for (let i = 0; i < a.length; i++) {
        max = Math.max(max, a[i].price)
    }
    return max
}
function getCoinData(arr, coinType) {
    for (let i = 0; i < arr.length; i++) {
        const item = arr[i]
        if (item.symbol.toLocaleLowerCase() == coinType.toLocaleLowerCase()) {
            return item
        }
    }
    return null
}
function getWallet(wallletList, coinType) {
    for (let i = 0; i < wallletList.length; i++) {
        let item = wallletList[i]
        if (item.name == coinType) {
            return item
        }
    }
}

