const axisLineColorDark = "#333333";
const crossTextBackgroundColorDark = "#373a40";

function getThemeOptions(theme) {
    const textColor = textColorDark;
    const gridColor = gridColorDark;
    const axisLineColor = axisLineColorDark;
    const crossLineColor = axisLineColorDark;
    const crossTextBackgroundColor = crossTextBackgroundColorDark;
    return {
        grid: {
            horizontal: {
                color: gridColor,
            },
            vertical: {
                color: gridColor,
            },
        },
        candle: {
            priceMark: {
                high: {
                    color: textColor,
                },
                low: {
                    color: textColor,
                },
            },
            tooltip: {
                text: {
                    color: textColor,
                },
            },
        },
        technicalIndicator: {
            tooltip: {
                text: {
                    color: textColor,
                },
            },
