function getLanguageOptions(language) {
    return {
        candle: {
            tooltip: {
                labels:
                    language === "zh-CN"
                        ? ["时间：", "开：", "收：", "高：", "低：", "成交量："]
                        : language === "zh-HK"
                            ? ["時間：", "開：", "收：", "高：", "低：", "成交量："]
                            : [
                                "Time: ",
                                "Open: ",
                                "Close: ",
                                "High: ",
                                "Low: ",
                                "Volume: ",
                            ],
            },
        },
    };
}
